html,
body {
  padding: 0;
  margin: 0; }

html {
  background: #f9f9f9;
  height: 100%; }

body {
  min-width: 320px;
  min-height: 100%;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-bottom: 160px;
  background: #fff;
  font-size: 14px;
  font-family: 'Helvetica Neue', 'Helvetica', tahoma, 'Hiragino Sans GB', 'PingFang SC', 'STHeitiSC-Light', 'Microsoft YaHei', Arial, sans-serif; }

body a {
  text-decoration: none; }

body a:hover {
    color: #40a9ff; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0; }

ul,
li,
dl,
dt,
dd {
  margin: 0;
  padding: 0; }

p {
  margin: 0; }

.main {
  padding-top: 56px; }

.blue {
  color: #1890ff; }

.green {
  color: #52c41a; }

.purple {
  color: #722ed1; }

.red {
  color: #f5222d; }

.gray {
  color: #bfbfbf; }

.delete {
  text-decoration: line-through; }

.clear::after {
  display: block;
  clear: both;
  content: '\20';
  height: 0; }

.hljs {
  color: #666; }

.markdown-body {
  color: #314659;
  font-size: 14px;
  line-height: 2; }

.markdown-body .center {
    margin: 0 auto;
    display: block; }

.markdown-body .thumb {
    width: 200px; }

.markdown-body .tip {
    color: #888; }

.markdown-body code {
    background-color: #abe090;
    color: #333; }

.markdown-body li > p {
    margin-bottom: 2px; }

video {
  width: 100%;
  margin: 10px 0 20px; }

video .not-support {
    line-height: 50px;
    text-align: center;
    color: #f00;
    border: 1px solid #ddd;
    padding: 50px; }
